-charcoal;\n    color: $pf-white;\n    border: 1px solid transparent;\n\n    &:hover,\n    &:focus {\n      background-color: transparent;\n      color: $pf-charcoal;\n      border-color: $pf-charcoal;\n    }\n  }\n}",".block-spacing {\n    &--top {\n        &.spacing--xs {\n            margin-bottom: 0;\n            margin-top: $spacing--xs;\n        }\n        &.spacing--sm {\n            margin-bottom: 0;\n            margin-top: $spacing--sm;\n        }\n        &.spacing--md {\n            margin-bottom: 0;\n            margin-top: $spacing--md;\n        }\n        &.spacing--lg {\n            margin-bottom: 0;\n            margin-top: $spacing--lg;\n        }\n        &.spacing--xl {\n            margin-bottom: 0;\n            margin-top: $spacing--xl;\n        }\n    }\n    &--bottom {\n        &.spacing--xs {\n            margin-top: 0;\n            margin-bottom: $spacing--xs;\n        }\n        &.spacing--sm {\n            margin-top: 0;\n            margin-bottom: $spacing--sm;\n        }\n        &.spacing--md {\n            margin-top: 0;\n            margin-bottom: $spacing--md;\n        }\n        &.spacing--lg {\n            margin-top: 0;\n            margin-bottom: $spacing--lg;\n        }\n        &.spacing--xl {\n            margin-top: 0;\n            margin-bottom: $spacing--xl;\n        }\n    }\n    &--both {\n        &.spacing--xs {\n            margin-top: $spacing--xs;\n            margin-bottom: $spacing--xs;\n        }\n        &.spacing--sm {\n            margin-top: $spacing--sm;\n            margin-bottom: $spacing--sm;\n        }\n        &.spacing--md {\n            margin-top: $spacing--md;\n            margin-bottom: $spacing--md;\n        }\n        &.spacing--lg {\n            margin-top: $spacing--lg;\n            margin-bottom: $spacing--lg;\n        }\n        &.spacing--xl {\n            margin-top: $spacing--xl;\n            margin-bottom: $spacing--xl;\n        }\n    }\n    &--none {\n        margin-top: 0;\n        marg